.single-news {
    padding: 6rem 0;
    position: relative;
    z-index: 2;

    @include media-breakpoint-up(lg) {
        padding: 10rem 0;
    }

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 0 35vw 35vw;
        border-color: transparent transparent $clr-grey-blue transparent;
        z-index: -1;
    }

    &__post-heading {
        @include font-size(3.5rem);
        letter-spacing: -0.077rem;
        line-height: 1.286;
        font-weight: bold;
        margin: 0 0 2rem;

        @include media-breakpoint-up(xxl) {
            font-size: 3.5rem;
        }
    }

    &__post-date {
        @include font-size(2.5rem);
        font-weight: 500;
        letter-spacing: -0.055rem;
        line-height: 1.36;
        color: $clr-grey-blue;
        margin: 0 0 3rem;
    }
}



